    getStation () {
      this.mode = 'station'

      if (!this.json || this.json['@datastream.id'] !== this.datastreamId) {
        this.json = null
        this.$http.get(this.feature.properties['Thing@iot.navigationLink'])
        .then(resp => {
          this.json = resp.body
          this.json['@datastream.id'] = this.datastreamId})
      }
    },
    getDataOld (e) {
